@import url(https://fonts.googleapis.com/css2?family=Lato:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Modak|Montserrat:500|Pacifico|Permanent+Marker&display=swap&subset=latin-ext);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



#container{
    margin-bottom: 1%;
    display: flex;
    flex-direction: row;
    height: 70px;
    align-items: center;
    
}

#user_login_form{
    display: flex;
    justify-content: center;
    align-content: space-between;
    margin-right: 1vw;
    position: absolute;
    right: 0px;
}



#NavBar_container{
    width: 80%;
    padding-top: 1%;
    padding-left: 3%;
    padding-right: 3%;
    padding-bottom: 1%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.nav_bar_link_body{
    width: 450px;
    display: flex;
    justify-content: space-between;
    
}

.nav_bar_link{
    background-color: orange;
    font-weight: bold;
    width: 130px;
    height: 50px;
    border-radius: 5px;
    font-family: 'Lato', sans-serif;
    color: rgb(39, 39, 39);
    box-shadow: 2px 5px #888888;
    
}

.nav_bar_link:hover{
    background-color: rgb(247, 224, 181);
    color: darkblue;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}
*{
    margin: 0px;
    padding: 0px;
    border: 0px;
}


.body{
    text-align: center;
    width: 100vw;
    height: auto;
    background-image: url(/static/media/background.d31eaa7a.jpg);
    background-repeat: repeat;
    background-attachment: fixed;
    background-size: cover;
}

.header{
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    height: 30%;
    width: 100%;
}

.header h1{
    font-family: Pacifico;
    font-size: 5vw;
    color: black;
}
.header_img{
    width: 20%;
    height:auto;
}

Table{
    border-radius: 10px;
    border: 0;
    background-color: rgba(45,45,45, 0.99) !important;
}

#timeStampNote{
    font-family: Pacifico;
}


